/**
 * base64
 * @param {*} file 
 */
export const compressImage = (imageFile, callback) => {
  const file = imageFile.file;
  const fileType = file.type;
  let fileReader = new FileReader();  
  fileReader.readAsDataURL(file);  
  fileReader.onload = event => {
    let result = event.target.result;   //dataURL  
    let image = new Image();  
    image.src = result;  
    image.onload = function(){  //imagecanvas  
      let cvs = document.createElement('canvas');  
      var scale = 1;    
      if(this.width > 500 || this.height > 500){  //800    
        if(this.width > this.height){    
          scale = 500 / this.width;  
        }else{    
          scale = 500 / this.height;    
        }    
      }  
      cvs.width = this.width * scale;    
      cvs.height = this.height * scale;     //  
      let ctx = cvs.getContext('2d');    
      ctx.drawImage(this, 0, 0, cvs.width, cvs.height);  
      let newImageData = cvs.toDataURL(fileType, 0.8);   //<span style="font-family: Arial, Helvetica, sans-serif;">fileType</span>  
      callback(newImageData);
    }
    return image;  
  }
}


